import React from "react";
import Disqus from "disqus-react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import { Helmet } from "react-helmet";
import { Flex, Box } from "@rebass/grid/";

export default ({ data }) => {
  const post = data.markdownRemark;
  return (
    <Layout>
      <Flex pt={5}>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{data.site.siteMetadata.title} | Contact</title>
          <link rel="" target="_blank" href="http://twitter.com/ilhamfadllah" />
        </Helmet>
        <div style={{ width: "100%" }}>
          <div
            dangerouslySetInnerHTML={{ __html: post.html }}
            style={{ lineHeight: "2em" }}
          />
          <DisqusComment
            blogId={post.frontmatter.disqusId}
            blogTitle={post.frontmatter.title}
          />
        </div>
      </Flex>
    </Layout>
  );
};

class DisqusComment extends React.Component {
  render() {
    const disqusShortname = "positiveneutron";
    const disqusConfig = {
      url: "https://positiveneutron.github.io/",
      identifier: this.props.blogId,
      title: this.props.blogTitle
    };

    return (
      <div className="article">
        <Disqus.CommentCount shortname={disqusShortname} config={disqusConfig}>
          Comments
        </Disqus.CommentCount>

        <Disqus.DiscussionEmbed
          shortname={disqusShortname}
          config={disqusConfig}
        />
      </div>
    );
  }
}

export const query = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        disqusId
        title
      }
    }
  }
`;
